import { Injectable } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';

@Injectable({
  providedIn: 'root'
})
export class NotificationService {
  // Duración base para los mensajes
  private defaultDuration = 4000;

  constructor(private snackBar: MatSnackBar) {}

  private open(message: string, type: 'success' | 'error' | 'warning' | 'info', duration?: number): void {
    this.snackBar.open(message, 'CERRAR', {
      duration: duration || this.defaultDuration,
      panelClass: [`custom-snackbar`, `${type}-snackbar`],
      horizontalPosition: 'center',
      verticalPosition: 'top',
      politeness: 'polite'
    });
  }

  showSuccess(message: string, duration?: number): void {
    this.open(message, 'success', duration);
  }

  showError(message: string, duration?: number): void {
    this.open(message, 'error', duration);
  }

  showWarning(message: string, duration?: number): void {
    this.open(message, 'warning', duration);
  }

  showInfo(message: string, duration?: number): void {
    this.open(message, 'info', duration);
  }
}