import { Component } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MatTableModule } from '@angular/material/table';
import { FormsModule } from '@angular/forms';
import { MatInputModule } from '@angular/material/input';
import { MatButtonModule } from '@angular/material/button';
import { MatDialog, MatDialogModule } from '@angular/material/dialog';
import { TableModule } from 'primeng/table';
import { ListPreciosService } from "../../services/list-precios.service";
import { finalize } from 'rxjs/operators'; // Importación correcta
import { NotificationService  } from '../../services/notificatios.service';

@Component({
  selector: 'app-historico-precios',
  standalone: true,
  imports: [
    CommonModule,
    MatTableModule,
    FormsModule,
    MatInputModule,
    MatButtonModule,
    MatDialogModule,
    TableModule,
  ],
  templateUrl: './historico-precios.component.html',
  styleUrls: ['./historico-precios.component.scss']
})
export class HistoricoPreciosComponent {
  displayedColumns: string[] = [
    'codigoProveedor',
    'codigoSap',
    'descripcion',
    'costoUnitario',
    'costoFinal',
    'descuento1',
    'descuento2',
    'costoFinalOC',     // Nuevo campo (precio bruto con descuentos)
    'variacion',
    'observacion'
  ];


  dataSource: any[] = [];
  loading = false;
  error = '';

  registroEditando: any = null;
  mostrarFormularioEdicion: boolean = false;

  constructor(
    private notificationService: NotificationService ,

    public dialog: MatDialog, private listPreciosService: ListPreciosService) { }
  ngOnInit(): void {
    this.cargarDatos();
  }


  cargarDatos(): void {
    this.loading = true;
    this.error = '';

    this.listPreciosService.getHistoricoPrecios()
      .pipe(
        finalize(() => this.loading = false)
      )
      .subscribe({
        next: (response) => {

          if (response.success) {
            this.dataSource = this.calcularVariacionYObservacion(response.data);
          } else {
            this.error = response.message || 'Error al cargar datos';
          }
        },
        error: (err) => {
          this.error = err.message || 'Error de conexión con el servidor';
        }
      });

  }
  private calcularVariacionYObservacion(data: any[]): any[] {
    return data.map(item => {
      // Calcular Costo Final OC (precio_bruto después de descuentos)
      const desc1 = item.descuento1 / 100;
      const desc2 = item.descuento2 / 100;
      const costoFinalOC = item.costo_unitario * (1 - desc1) * (1 - desc2);

      // Calcular variación porcentual (comparando costo_unitario vs costoFinalOC)
      const variacion = ((costoFinalOC - item.precio_bruto) / item.precio_bruto) * 100;

      // Determinar observación
      let observacion = 'IGUAL';
      if (variacion > 0) {
        observacion = 'SUBE';
      } else if (variacion < 0) {
        observacion = 'BAJA';
      }

      return {
        ...item,
        costoFinalOC: parseFloat(costoFinalOC.toFixed(2)),
        variacion: parseFloat(variacion.toFixed(2)),
        observacion: observacion,
        // Mapeo de nombres para mantener compatibilidad
        costoUnitario: item.costo_unitario,
        codigoProveedor: item.cod_prov,
        codigoSap: item.cod_sap,
        costoFinal: item.precio_bruto
      };
    });
  }
  editarRegistro(registro: any): void {
    this.registroEditando = { ...registro };
    this.mostrarFormularioEdicion = true;
  }

  guardarCambios(): void {
    const index = this.dataSource.findIndex(item => item.id === this.registroEditando.id);
    if (index !== -1) {
      // Recalcular costo final al guardar
      this.registroEditando.costoFinal = this.calcularCostoFinal();
      this.dataSource[index] = { ...this.registroEditando };
    }
    this.cancelarEdicion();
  }

  calcularCostoFinal(): number {
    const desc1 = this.registroEditando.descuento1 / 100;
    const desc2 = this.registroEditando.descuento2 / 100;
    return this.registroEditando.costoUnitario * (1 - desc1) * (1 - desc2);
  }

  cancelarEdicion(): void {
    this.registroEditando = null;
    this.mostrarFormularioEdicion = false;
  }

  eliminarRegistro(id: number): void {
    this.dataSource = this.dataSource.filter(item => item.id !== id);
  }




  // Agrega al inicio de la clase
  editableColumns = ['costo_unitario', 'descuento1', 'descuento2'];
  editMode: { [key: string]: { [field: string]: boolean } } = {};

  // Métodos para edición
  startEditing(item: any, field: string): void {
    if (!this.editMode[item.id]) this.editMode[item.id] = {};
    this.editMode[item.id][field] = true;
  }

  saveEditing(item: any, field: string): void {
    if (this.editMode[item.id]) {
      this.editMode[item.id][field] = false;

      this.recalcularValores(item);
    }
  }

  private recalcularValores(item: any): void {
    const desc1 = item.descuento1 / 100;
    const desc2 = item.descuento2 / 100;
    item.costoFinalOC = item.costo_unitario * (1 - desc1) * (1 - desc2);

    const variacion = ((item.costoFinalOC - item.precio_bruto) / item.precio_bruto) * 100;
    item.variacion = parseFloat(variacion.toFixed(2));

    item.observacion = 'IGUAL';
    if (variacion > 0) item.observacion = 'SUBE';
    if (variacion < 0) item.observacion = 'BAJA';
  }

  isEditing(item: any, field: string): boolean {
    return this.editMode[item.id] && this.editMode[item.id][field];
  }
guardarTodosPrecios() {
  const preciosActualizados = this.dataSource.map(item => ({
    id: item.id,
    cod_prov: item.cod_prov,
    cod_sap: item.cod_sap,
    costo_unitario: item.costo_unitario,
    descuento1: item.descuento1,
    descuento2: item.descuento2,
    precio_bruto: item.precio_bruto,
    costoFinalOC: item.costoFinalOC,
    variacion: item.variacion,
    observacion: item.observacion
  }));

  this.listPreciosService.guardarCambiosPrecios(preciosActualizados)
    .subscribe({
      next: (response:any) => {
        console.log(response.data.message);
        
        if (response.success) {
          this.notificationService.showSuccess(response.data.message || 'Precios actualizados correctamente');
          this.editMode = {};
        } else {
          this.notificationService.showWarning(response.data.message || 'No se realizaron cambios');
        }
      },
      error: (err) => {
        const errorMsg = err.error?.message || err.message || 'Error al guardar los precios';
        this.notificationService.showError(errorMsg);
      }
    });
}
  // Reemplaza showSuccess por:
  private showSuccess(message: string): void {
    this.notificationService.showSuccess(message);
  }

  private showError(message: string): void {
    this.notificationService.showError(message);
  }


}